.hesder{
    margin: 10px 40px 0 40px;
    min-width: 94%;
}
.overlay{
    background-color: rgb(247, 240, 240);
}
.nav_gamburger{
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100vh;
    background-color: rgb(73, 72, 72);
    text-align: center;
    font-size: 25px;
    z-index: 10;
    .close{
        position: absolute;
        top: 10px;
        right: 15px;
        font-size: 35px;
    }
    .gamburger_ul{
        margin-top: 50px;
    }
    li{
        list-style-type: none;
    }
}
@media (max-width:991px){
    .ul{
        display: flex;
        justify-content: flex-start;
    }
    .nav{
        display: none;
    }
    .gamburger{
        display: flex;
        flex-direction: column;
        span{
            width: 30px;
            height: 4px;
            margin-top: 4px;
            background-color: #fff;

        }
    }
}